.inline-link::after{
  background-color:var(--link-colour);
  height:2px !important
}

.link-hover-effect,.horizontal-link-hover-effect{
  display:inline-block;
  position:relative
}

.horizontal-link-hover-effect::after{
  content:'';
  position:absolute;
  display:block;
  height:3px;
  width:100%;
  transition:transform 400ms;
  transform:scaleX(0);
  transform-origin:right
}

.horizontal-link-hover-effect:hover::after{
  transform:scaleX(1);
  transform-origin:left
}

.link-hover-effect::after{
  content:'';
  position:absolute;
  display:block;
  height:3px;
  width:0;
  transition:width 400ms ease
}

.link-hover-effect:hover::after{
  width:100%
}

.toggle-switch{
  position:relative;
  display:inline-block;
  width:42px;
  height:24px
}

.toggle-switch input{
  opacity:0;
  width:0;
  height:0
}

.slider{
  position:absolute;
  cursor:pointer;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background-color:#cccccc;
  transition:.4s
}

.slider.round{
  border-radius:25px
}

.slider.round:before{
  border-radius:50%
}

.slider:before{
  position:absolute;
  content:"";
  height:18px;
  width:18px;
  left:3px;
  bottom:3px;
  background-color:white;
  transition:.4s
}

input:checked+.slider{
  background-color:#2196F3
}

input:focus+.slider{
  box-shadow:0 0 1px #2196F3
}

input:checked+.slider:before{
  transform:translateX(18px)
}

.tooltip{
  position:relative;
  justify-content:center
}

.tooltip::before{
  content:attr(tooltip-text);
  right:50%;
  transition:all ease-in-out 400ms;
  white-space:nowrap;
  overflow:hidden;
  display:inherit;
  visibility: hidden;
  width: 0px;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity))
}

.dark .tooltip::before{
  --tw-bg-opacity: 1;
  background-color: rgba(31, 41, 55, var(--tw-bg-opacity))
}

