Body.dark{
  --primary-background: #1f2129;
  --secondary-background: rgb(39, 41, 52);
  --highlight-primary: rgb(129, 91, 233);
  --highlight-primary-accent: rgb(170, 147, 255);
  --highlight-secondary: rgb(255, 164, 0);
  --highlight-secondary-hover: #db8f00;
  --text-primary: rgb(242, 244, 251);
  --text-primary-hover: rgb(226, 226, 226);
  --text-secondary: #bdbdbd;
  --link-colour: #9cd3ff
}

Body.light{
  --primary-background: #e1e3ec;
  --secondary-background: rgb(255, 255, 255);
  --highlight-primary: rgb(129, 91, 233);
  --highlight-primary-accent: rgb(170, 147, 255);
  --highlight-secondary: rgb(255, 164, 0);
  --highlight-secondary-hover: #db8f00;
  --text-primary: rgb(20, 20, 20);
  --text-secondary: #464646;
  --link-colour: #0079db
}

*{
  --body-width: 1100px;
  --section-width: 80%;
  --background-offset: 15px;
  --sidebar-width: 18%
}

.centred-body{
  width:var(--body-width);
  left:50%;
  margin-left:calc(-1 * var(--body-width) / 2);
  position:relative
}

.header{
  display:flex;
  justify-content:space-between
}

.header-background{
  width:100%;
  background-color:var(--highlight-primary)
}

.header-group{
  margin-bottom:50px
}

.header-text{
  display:flex;
  flex-direction:column;
  align-items:flex-end;
  position:relative
}

.header-text h1{
  font-size:80px;
  -webkit-margin-after:0;
  margin-block-end:0;
  -webkit-margin-before:0.1em;
  margin-block-start:0.1em
}

.header-secondary{
  background:var(--highlight-primary-accent);
  padding:8px;
  position:relative
}

.profile-image{
  margin-top:30px;
  margin-bottom:-50%;
  height:-webkit-min-content;
  height:min-content;
  z-index:1;
  width:var(--sidebar-width)
}

.profile-image img{
  width:100%;
  border-radius:50%
}

.profile-links{
  top:-10px;
  position:relative;
  display:flex;
  flex-direction:row;
  justify-content:right
}

.profile-links a{
  margin-left:20px;
  color:var(--text-primary) !important;
  transition:transform .2s ease
}

.profile-links a:hover{
  transform:translate3d(0, -0.2em, 0)
}

.navigation-buttons{
  display:flex;
  flex-direction:row;
  justify-content:right;
  align-items:center;
  font-size:1.2rem
}

.nav-button{
  color:var(--text-primary);
  font-weight:bold;
  font-size:22px;
  margin:-5px 0px -5px 50px
}

.nav-button::after{
  background-color:var(--text-primary)
}

.nav-button.btn:hover{
  color:var(--text-primary-hover)
}

.github-link::after{
  --tw-bg-opacity: 1;
  background-color: rgba(139, 92, 246, var(--tw-bg-opacity))
}

.dark .github-link::after{
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity))
}

@media only screen and (max-width: 1099px){
  *{
    --body-width: 90%
  }
}

@media only screen and (max-width: 850px){
  *{
    --content-width: 90%;
    --content-overlap: 25%
  }

  .profile-image img{
    display:none
  }

  .header-text h1{
    font-size:70px;
    margin-bottom:10px
  }

  .header-secondary{
    display:none
  }
}

@media only screen and (max-width: 500px){
  *{
    --content-width: 100%
  }

  .header-text h1{
    font-size:50px
  }
}

